import React, { useState } from "react";
import "./App.css";
import Navbar from "./components/Navbar";
import Textform from "./components/Textform";
import Alert from "./components/Alert";

function App() {
  const [mode, setMode] = useState("light");
  const [alert, setAlert] = useState(null);

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type,
    });

    setTimeout(()=>{setAlert(null)},1200)
  };

  const togglemode = () => {
    if (mode === "light") {
      setMode("dark");
      document.body.style.backgroundColor = "#495057";
      showAlert("Dark Mode Enabled Successfuly", "success");
    } else {
      setMode("light");
      document.body.style.backgroundColor = "#ffffff  ";
      showAlert("Light Mode Enabled Successfuly", "success");

    }
  };

  return (
    <React.Fragment>
      <Navbar modes={mode} togglemode={togglemode} />
      <Alert alerts={alert} />
      <Textform modes={mode} showAlert={showAlert} />
    </React.Fragment>
  );
}

export default App;



