import React, { useState } from "react";

export default function Textform(props) {
  let [text, setText] = useState("");

  const handlechange = (e) => {
    setText(e.target.value);
  };
  const handleUpper = () => {
    if (text === "") {
      props.showAlert("Textbox is empty", "warning");
    } else {
      setText(text.toUpperCase());
      props.showAlert("Text is converted in UpperCase", "success");
    }
  };
  const handleLower = () => {
    if (text === "") {
      props.showAlert("Textbox is empty", "warning");
    } else {
      setText(text.toLowerCase());
      props.showAlert("Text is converted in LowerCase", "success");
    }
  };
  const handleClear = () => {
    if (text === "") {
      props.showAlert("Textbox is already empty", "warning");
    } else {
      setText((text = ""));
      props.showAlert("Text Cleared", "success");
    }
  };
  const handleSpaces = () => {
    if (text === "") {
      props.showAlert("Textbox is empty", "warning");
    } else {
      text = text.split(/[ ]+/);
      setText(text.join(" "));
      props.showAlert("Extra Spaces removed", "success");
    }
  };
  const copytoClipboard = () => {
    if (text === "") {
      props.showAlert("Textbox is empty", "warning");
    } else {
      window.navigator.clipboard.writeText(text);
      props.showAlert("Text copied", "success");
    }
  };
  const copyNumber = () => {
    if (text === "") {
      props.showAlert("Textbox is empty", "warning");
    } else if(text.match(/\d+/g)){
      text = text.match(/\d+/g);

      window.navigator.clipboard.writeText(text.join(" "));
      props.showAlert("Digits copied", "success");

    }
    else{
      props.showAlert("No Digit found", "warning");

    }
  };

  const copyAlphabets = () => {
    if (text === "") {
      props.showAlert("Textbox is empty", "warning");
    } else if(text.match(/[a-zA-Z]+/g)) {
      text = text.match(/[a-zA-Z]+/g);

      window.navigator.clipboard.writeText(text.join(" "));
      props.showAlert("Alphabets are copied", "success");

    }
    else{
      props.showAlert("No Alphabet found", "warning");

    }
  };
  const copySpecialCharacters = () => {
    if (text === "" ) {
      props.showAlert("Textbox is empty", "warning");
    }

    else if(text.match(/[^\w\s]+/g)){
      text = text.match(/[^\w\s]+/g);

      window.navigator.clipboard.writeText(text.join(" "));
      props.showAlert("Special Characaters copied", "success");

    }

    else{
      props.showAlert("No Special character found", "warning");

    }
  };

  const wordcount = text.trim() === "" ? 0 : text.trim("").split(/\s+/).length;
  return (
    <div className={`container `}>
      <div
        className={`my-3 text-${props.modes === "light" ? "dark" : "white"}`}
      >
        <h1>Enter text here</h1>
        <label
          htmlFor="exampleFormControlTextarea1"
          className="form-label"
        ></label>
        <textarea
          className="form-control"
          id="exampleFormControlTextarea1"
          rows="12"
          placeholder="Write text here"
          onChange={handlechange}
      
          style={{
            backgroundColor: props.modes === "light" ? "white" : "#495057",
            color: props.modes === "light" ? "#000" : "#fff",
          }}
          value={text}
        ></textarea>
      </div>
      <div>
        <button className="btn btn-primary m-2" onClick={handleUpper}>
          Convert text to UpperCase
        </button>
        <button className="btn btn-primary m-2" onClick={handleLower}>
          Convert text to LowerCase
        </button>
        <button className="btn btn-primary m-2" onClick={copytoClipboard}>
          Copy text
        </button>
        <button className="btn btn-primary m-2" onClick={handleSpaces}>
          Remove Extra Spaces
        </button>
        <button className="btn btn-primary m-2" onClick={copyNumber}>
          Copy Digits
        </button>
        <button className="btn btn-primary m-2" onClick={copyAlphabets}>
          Copy Alphabetics
        </button>
        <button className="btn btn-primary m-2" onClick={copySpecialCharacters}>
          Copy Special characters
        </button>

        <button className="btn btn-primary mx-2" onClick={handleClear}>
          Clear Text
        </button>
      </div>

      <div
        className={`my-3 text-${props.modes === "light" ? "dark" : "white"}`}
      >
        <h1>Text Summary</h1>

        <p>
          {" "}
          {wordcount} word {text.length} Characters
        </p>

        <h1>Preview</h1>
        <p>
          {text.trim() === "" ? "Enter text in above box to preview" : text}
        </p>
      </div>
    </div>
  );
}



